@media screen and (min-width: 820px) and (min-height: 820px) {
    .footer {
        position: fixed;
        bottom: 0;
    }
}

@media screen and (max-width: 819px) and (min-height: 1210px) {
    .footer {
        position: fixed;
        bottom: 0;
    }
}

.footer {
    color: var(--white-text-color);
    background-color: var(--denim-bg-color);
    display: flex;
    justify-content: space-between;
    width: 90%;
    height: 90px;
    margin-top:20px;
    padding: 0 5%;
    border-top: 2px solid var(--gray-border-color);
}

.footer_left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}

.footer_left span {
    margin: 5px 0;
    padding: 0;
}

.footer_right {
    display: flex;
    align-items: center;
    height: 100%;
}

.footer_right img{
    width: auto;
    height: 30px;
}