.flight_strip {
    background-color: rgba(255, 255, 255, .7);
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}

.flight_strip table {
    margin: 5px;
    border-collapse: collapse;
}

.flight_strip td {
    font-size: 14px;
    font-weight: 500;
    vertical-align: top;
    width: 85px;
    max-width: 85px;
    padding: 5px;
    border-right: 1px solid var(--white-border-color);
}

.flight_strip .first_row{
    padding: 0 5px;
}

.flight_strip .first_row span{
    font-size: 24px;
    cursor: pointer;
}

.flight_strip .first_row,
.flight_strip #last_col {
    border-right: none;
}

#last_col {
    width: 300px;
    max-width: 300px;
}

.flight_strip .add_divider {
    border-bottom: 1px solid var(--white-border-color);
}