.flight_container {
    color: var(--white-text-color);
    position: absolute;
    width: 80px;
    height: 35px;
    /* border: 1px solid red; */
    transform: translate(-6px, -30px);
    user-select: none;
}

.flight_info {
    font-size: 11px;
    margin-left: 18px;
}

#flight_logo {
    font-size: 10px;
    font-weight: bold;
    text-align: center;
    color: var(--black-text-color);
    background-color: var(--white-bg-color);
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 11px;
    height: 11px;
    border-radius: 50%;
}

#connector {
    font-size: 22px;
    line-height: 22px;
    font-weight: 300;
    position: absolute;
    left: 8px;
    bottom: 6px;
}