.cmd_guide {
    background-color: var(--white-bg-color);
    width: 300px;
    border-radius: 20px;
    box-shadow: 0 0 10px var(--black-shadow-color);
    position: absolute;
    top: 85px;
    right: 0;
    z-index: 1;
}

.guide_container {
    width: calc(100% - 40px);
    padding: 20px;
}

.guide_container h2 {
    font-size: 24px;
    font-weight: 500;
    text-align: center;
    margin: 0 0 40px;
    padding: 0;
}

.example_cmd {
    font-size: 24px;
    display: flex;
    justify-content: space-between;
    width: 170px;
    margin: 20px auto;
}

.example_cmd div {
    padding: 5px 2px 0;
    border-top-width: 2px;
    border-top-style: solid;
}

.example_fight_id {
    border-color: var(--blue-color);
}

.example_cmd_type {
    border-color: var(--red-color);
}

.example_cmd_value {
    border-color: var(--green-color);
}

.instructions {
    margin-left: 40px;
}

.instructions p{
    margin: 12px 0 0;
    padding-left: 5px;
    border-left-width: 2px;
    border-left-style: solid;
}

.horizontal_line div {
    border-width: 2px 2px 0;
    border-style: solid;
}

.horizontal_line{
    position: absolute;
    top: 92px;
    right: 87px;

    .example_cmd_value {
        width: 184px;
        height: 26px;
    }

    .example_cmd_type {
        width: 134px;
        height: 17px;
        position: absolute;
        top: 9px;
        left: 9px;
    }

    .example_fight_id {
        width: 63px;
        height: 8px;
        position: absolute;
        top: 7px;
        left: 8px;
    }
}

.vertical_line div {
    border-width: 0 0 2px 2px;
    border-style: solid;
}

.vertical_line{
    position: absolute;
    top: 120px;
    left: 25px;

    .example_cmd_value {
        width: 35px;
        height: 162px;
    }

    .example_cmd_type {
        width: 25px;
        height: 104px;
        position: absolute;
        left: 9px;
    }

    .example_fight_id {
        width: 16px;
        height: 63px;
        position: absolute;
        left: 8px;
    }
}