.keyboard {
    background-color: var(--white-bg-color);
    display: flex;
    flex-direction: column;
    width: 500px;
    padding: 2.5px;
    box-shadow: 0 0 8px 0 var(--black-shadow-color);
    position: absolute;
    top: 130px;
    right: 0;
    z-index: 2;
    cursor: move;
    user-select: none;
}
.keyboard_row {
    font-size: 14px;
    display: flex;
    justify-content: space-around;
    margin: 2.5px 0;
}

.keyboard_row div {
    text-align: center;
    vertical-align: middle;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 45px;
    outline: 1px solid var(--gray-border-color);
    cursor: pointer;
}

.blue_btn,
.green_btn,
.special_btn,
.yellow_btn,
.white_btn {
    width: 45px;
}

.blue_btn {
    background-color: var(--cyan-color);
}

.green_btn {
    background-color: var(--islamic-green-color);
}

.red_btn {
    background-color: var(--red-color);
    width: 95px;
}

.yellow_btn,
.special_btn,
.long_yellow_btn {
    background-color: var(--yellow-color);
}

.long_yellow_btn {
    width: 295px;
}

.special_btn {
    font-size: 24px;
}

.close_btn {
    font-size: 24px;
    color: var(--red-color);
    text-align: right;
    margin: 2.5px;
}

.close_btn svg {
    cursor: pointer;
}