:root {
  /* Font's Colors */
  --white-text-color: #fff;
  --gray-text-color: #808080;
  --black-text-color: #0f0f0f;
  /* Background Colors */
  --white-bg-color: #fff;
  --white-smoke-bg-color: #f6f6f6;
  --denim-bg-color: #2f4050;
  /* Border and shadow Colors */
  --white-border-color: #fff;
  --gray-border-color: #b7b7b7;
  --black-border-color: #0f0f0f;
  --black-shadow-color: rgba(0, 0, 0, 0.3);
  /* Common color Color */
  --blue-color: #0000FF;
  --cyan-color: #80FFFF;
  --green-color: #008000;
  --islamic-green-color: #00C000;
  --red-color: #FF0000;
  --yellow-color: #FFFF00;
}

::placeholder {
  color: var(--gray-text-color);
}

@media screen and (max-width: 819px) {
  .aside {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
  }
  .top_right_section {
    margin-top: 20px;
  }
}

html, body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  color: var(--black-text-color);
  background-color: var(--white-smoke-bg-color);
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

.root {
  width: 100%;
  height: 100%;
}

.main, .aside {
  margin: 0;
  padding: 0;
  position: relative;
}

.main {
  width: 604px;
  height: 604px;
}

.top_right_section {
  position: relative;
}