/* Styles for Button */
.btn_area {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 65px;
    height: 30px;
    padding: 0;
    margin: 0;
}

.button{
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    color: var(--black-text-color);
    background-color: transparent;
    border: none;
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0 15px;
    cursor: pointer;
}

.button svg {
    height: 20px;
}


/* Styles of Playback Speed */
.playback_speed_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 150px;
    z-index: 1;
    position: absolute;
    top: 43%;
    border-radius: 10px;
    overflow: hidden;
}

.playback_speed_container button {
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    vertical-align: middle;
    color: var(--white-text-color);
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    height: 20%;
    border: none;
}

.playback_speed_container button:hover {
    background-color: rgba(77, 77, 77, 0.8);
}