.controller_container {
    background-color: var(--white-bg-color);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    width: 200px;
    min-width: 200px;
    max-height: 170px;
    position: relative;
    padding: 5px 0;
    border-radius: 5px;
    box-shadow: 0 0 8px 0 var(--black-shadow-color);
}

.controller_container > div {
    padding: 5px 0;
}

.timer {
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 65px;
    height: 30px;
    padding: 0;
    margin: 0;
    border: 0;
    user-select: none;
    /* background-color: skyblue; */
}