.command input,
.get_flight_strip {
    margin: 0 10px;
    padding: 0 5px;
    border: none;
    border-radius: 3px;
    border: 1px solid var(--gray-border-color);
}

.command input {
    width: 170px;
    height: 30px;
}

.command input:focus,
.get_flight_strip:focus {
    color: var(--black-text-color);
    outline: none;
    border: 1px solid var(--black-border-color);
}

.get_flight_strip {
    color: var(--gray-text-color);
    width: 182px;
    height: 32px;
    appearance: none;
}

.active_strip {
    color: var(--black-text-color);
}

.get_flight_strip option {
    font-size: 14px;
    color: var(--white-text-color);
    background-color: rgba(0, 0, 0, 0.8);
}

.flight_strip_container {
    position: relative;
}

.flight_strip_container .select_arrow {
    color: var(--gray-text-color);
    position: absolute;
    top: 12px;
    right: 16px;
    pointer-events: none;
}

.get_flight_strip:focus+.select_arrow {
    color: var(--black-text-color);
}