@media screen and (max-width: 819px) {
    .main_contents {
        flex-direction: column;
        align-items: center;
    }
}

.app {
    position: relative;
    width: 100%;
    min-width: 604px;
    height: 100%;
    overflow: hidden;
}

.main_contents {
    display: flex;
    justify-content: space-evenly;
    margin: 0;
    padding: 0;
    min-height: 600px;
}